.top-copy {
  background-image: url("/bg.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $white;
  justify-content: center;

  h1 {
    font-size: $h1;
  }
}

.services {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: $primary-color;
  padding: 5%;

  h3 {
    color: $white;
  }

  .services-list {
    padding: 5% 0;
    margin: 3% 10% 5% 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: $white;
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.1);
    border-radius: 10px;

    li {
      display: flex;
      align-items: center;
      width: 35%;
      justify-content: space-between;

      &:first-child,
      &:nth-child(2) {
        margin-bottom: 3rem;
      }

      .icon {
        color: $primary-color;
        font-size: 3rem;
      }
    }
  }
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5%;
  div {
    padding: 2rem 0;
    h4 {
      text-align: center;
    }
    ul {
      min-width: 500px;
      li {
        display: flex;
        align-items: center;
        margin: 1rem;
        h5 {
          color: $primary-color;
          font-size: $h5;
          text-align: right;
          width: 4rem;
        }
        p {
          padding-left: 1rem;
          margin-left: 1rem;
          border-left: 1px solid $primary-color;
        }
      }
    }  
  }
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5%;
  background-color: $primary-color;

  h3 {
    color: $white;
  }
  p {
    color: $white;
    text-align: center;
  }
  #typeform {
    width: 50%; 
    height: 350px;
    padding: 2rem;
  }
}